.menubox{
      display: flex;
      justify-content: right;
}

.menu{
      width: 100%;
      height: 275px;
      position: fixed;
      transition: .25s linear;
      color: rgb(0, 0, 0);
      background-image: linear-gradient(45deg, black,rgb(16, 16, 100),rgb(18, 18, 131),rgb(33, 33, 192),rgb(15, 15, 128),rgb(14, 14, 100),black);
      background-size: 1000% 100%;
      animation: change 60s ease infinite alternate;
      top: -400px;
      z-index: 3;
      display: flex;
      transition: all .5s ease-in-out;
      flex-direction: column;
      align-items: center;
      justify-content: flex-end;

      &.active{
            top: 0;
      }
}

ul.menulist{
      width: 99%;
      justify-content: center;
}

ul.menulist li.menuboxes{
      display: flex;
      width: 100%;
}

ul.menulist li.menuboxes a.menuitems{
      border: solid 1px rgb(255, 255, 255);
      width: 100%;
      justify-content:center;
      color: white;
      text-decoration: none;
      font-size: 25px;
      font-weight: 600;
      font-family: Arial, Helvetica, sans-serif;
      display: flex;
      padding: 10px 0px;
      border-radius: 10px;
}

ul.menulist li.menuboxes a.menuitems:hover{
      transition: .25s linear;
      color: rgb(0, 0, 0);
      background-image: linear-gradient(45deg,#00eeff, #f1b555,#00eeff);
      background-size: 10000% 500%;
      animation: change 20s ease infinite alternate;
}

@keyframes change{
      0%{
            background-position: 0%;
      }
      50%{
            background-position: 100%;
      }
      100%{
            background-position: 0%;
      }
}


@media (min-width: 768px) {
      .menu{
            display: none;
      }
}