.pagefour{
      display: flex;
      align-items: center;
      justify-content: center;
      font-family: 'EB Garamond', serif;
      font-size: 25px;
      font-weight: 600;
      background-image: linear-gradient(45deg,black,rgb(19, 24, 48),rgb(32, 43, 94),rgb(122, 164, 255),rgb(167, 189, 235)
      , rgb(225, 175, 235),rgb(167, 189, 235),rgb(122, 164, 255), rgb(32, 43, 94), rgb(19, 24, 48),#000000);
      background-size: 2500% 100%;
      animation: change 60s ease infinite alternate;  
}

.pagefour ul.experiencebg{
      z-index: 2;
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      flex-direction: column;
      padding: 25px;
      height: 750px;
}

ul.experiencebg ul.experiencestitlebox{
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100px;
      padding: 10px;
}

ul.experiencebg li.experiencestitle{
      display: flex;
      justify-content: center;
      align-items: center;
      height: 75px;
      margin: 25px;
      font-size: 30px;
      font-weight: 700;
      width: 225px;
      border-radius: 40px;
      margin-bottom: 3px;
      border: 3px solid black;

      transition: .25s linear;
      background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
      #ff0000,#00fffb,#fab64f,#00fffb);
      background-size: 2500% 100%;
      animation: change 30s ease infinite alternate;   
}

.pagefour ul.experiencelistrowone{
      display: flex;
      padding: 15px;
      height: 400px;
      justify-content: center;
}

ul.experiencelistrowone li.experience a{
      z-index: 4;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 75px;
      margin: 25px;
      font-size: 30px;
      font-weight: 700;
      width: 300px;
      border-radius: 40px;
      margin-bottom: 5px;
      text-decoration: none;
      color: rgb(255, 255, 255);
      border: 5px solid black;
      background-color: rgb(0, 0, 0);
}

ul.experiencelistrowone li.experience a:hover{
      color: black;
      text-decoration: underline;
      transition: .25s linear;
      background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
      #ff0000,#00fffb,#fab64f,#00fffb);
      background-size: 2500% 100%;
      animation: change 30s ease infinite alternate;
}


@media (max-width: 1100px){
      .pagefour ul.experiencelistrowone{
            flex-direction: column;
      }
}