.pagetwo{
      background-color:  rgb(19, 24, 48);
      display: flex;
      align-items: center;
      overflow: hidden;

      .leftside{
            flex: .5;
            overflow: hidden;
            align-items: center;
            justify-content: center;
            display: flex;

            .selfiecontainer{
                  height: 800px;
                  width: 800px;
                  background-color: rgb(122, 164, 255);
                  border-radius: 50%;
                  display: flex;
                  align-items: center;
                  justify-content: center;
            }
      }

      .rightside{
            flex: .5;
            display: flex;
            overflow: hidden;
            justify-content: center;
            align-items: center;
            height: 800px;
      }
}

.selfiecontainer .selfie{
      z-index: 2;
      background-color: rgb(122, 164, 255);
      height: 175%;
}

ul.insideright{
      display: flex;
      flex-direction: column;
      overflow: hidden;
      width: 500px;
      height: 500px;
      font-family: 'EB Garamond', serif;
      font-size: 30px;
      font-weight: 600;
      justify-content: center;
      align-items: center;
      background: rgb(167, 189, 235);
      color: rgb(0, 0, 0);
      padding: 25px 25px;

      li.aboutme{
            align-items: center;
            display: flex;
            width: 175px;
            height: 50px;
            overflow: hidden;
            justify-content: center;
            font-size: 30px;
            font-weight: 700;
            border-radius: 50px;
            border: 3px solid black;

            transition: .25s linear;
            background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
            #ff0000,#00fffb,#fab64f,#00fffb);
            background-size: 2500% 100%;
            animation: change 25s ease infinite alternate;   
}

      li.firstpabout{
            display: flex;
            align-items: center;
            height: 350px;
            padding-top: 25px;
            overflow: hidden;
      }
      li.secondpabout{
            align-items: center;
            display: flex;
            height: 150px;
            overflow: hidden;
      }
      li.thirdpabout{
            align-items: center;
            display: flex;
            height: 125px;
            overflow: hidden;
      }
}

@media (max-width: 1100px){
      .pagetwo{
            flex-direction: column;
            .leftside{
                  
                  flex: .35;
                  display: flex;
                  align-items: center;
                  
                  .selfiecontainer{
                        height: 500px;
                        width: 500px;
                  }
            }
            .rightside{
                  flex: .65;
                  width: 1100px;
                  height: 1100px;

                  .insideright{
                        height: 500px;
                        width: 800px;

                        li.firstpabout{
                              height: 350px;
                        }
                        li.secondpabout{
                              height: 125px;
                        }
                        li.thirdpabout{
                              height: 125px;
                        }
                  }
            }
      }
}

@media (max-width: 700px) {
      .pagetwo{
            .leftside{
                  flex: 0;
            }

            .rightside{
                  flex: 1;
                  
                  ul.insideright{
                        font-size: 25px;
                        height: 500px;
                        width: 500px;
                  }
            }
      }
}