.pagefive{
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: 'EB Garamond', serif;
      font-size: 25px;
      font-weight: 600;
      background-color: rgb(0, 0, 0);
}

.pagefive .contactbg{
      z-index: 2;
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 800px;
      background-color:  rgb(19, 24, 48);
      border-radius: 40px;
}

.contactbg .namebox{
      display: flex;
      justify-content: space-between;
      height: 150px;
      width: 700px;
      background-color:  rgb(19, 24, 48);
      margin: 15px 0px;
      padding: 10px;
      padding-bottom: 15px;
}

.contactbg .emailbox{
      justify-content: space-between;
