.contactbg .contacttitle{
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 50px;
      font-weight: 700;
      height: 325px;
      margin: 25px 0px;
      width: 300px;
      border-radius: 40px;
      border: 3px solid black;
      transition: .25s linear;
      background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
      #ff0000,#00fffb,#fab64f,#00fffb);
      background-size: 2500% 100%;
      animation: change 60s ease infinite alternate;
}

