.contactbg .buttoninfo{
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 30px;
      font-weight: 700;
      height: 100px;
      width: 500px;
      margin-top: 50px;
      border-radius: 40px;
      background-color:  rgb(0, 0, 0);
      color: white;
      cursor: pointer;
}

.contactbg .buttoninfo:hover{
      color: black;
      transition: .25s linear;
      border: 3px solid black;
      background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
      #ff0000,#00fffb,#fab64f,#00fffb);
      background-size: 1000% 100%;
      animation: change 15s ease infinite alternate;
      text-decoration: underline;
}

::placeholder {
      color: rgb(255, 255, 255);
}

.videogalaxy{
      display: flex;
      position: absolute;
      z-index: 0;
}
.galaxyvid{
      width: 100%;
      object-fit: contain;
}