.pagethree{
      display: flex;
      height: 500px;
      font-family: 'EB Garamond', serif;
      font-size: 25px;
      font-weight: 600;

      .leftsidepthree{
            flex: .5;
            flex-direction: column;
            display: flex;
            background-color: black;
            align-items: center;

            .hardskills{
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  height: 75px;
                  margin: 25px;
                  font-size: 30px;
                  font-weight: 700;
                  width: 200px;
                  border-radius: 40px;
                  margin-bottom: 5px;
                  border: 3px solid black;

                  transition: .25s linear;
                  background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
                  #ff0000,#00fffb,#fab64f,#00fffb);
                  background-size: 2500% 100%;
                  animation: change 30s ease infinite alternate;          
            }

            ul.hardskilllist{
                  display: flex;
                  transition: .25s linear;
                  background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
                  #ff0000,#00fffb,#fab64f,#00fffb);
                  background-size: 2000% 100%;
                  animation: change 50s ease infinite alternate;   

                  flex-direction: column;
                  padding: 10px;
                  height: 700px;
                  align-items: center;

                  li{
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 500px;
                        background: rgb(167, 189, 235);
                  }
            }
      }

      .rightsidepthree{
            flex: .5;
            flex-direction: column;
            background-color:  rgb(19, 24, 48);
            display: flex;
            align-items: center;

            .softskills{
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  height: 75px;
                  margin: 25px;
                  font-size: 30px;
                  font-weight: 700;
                  width: 200px;
                  border-radius: 40px;
                  margin-bottom: 5px;
                  border: 3px solid black;

                  transition: .25s linear;
                  background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
                  #ff0000,#00fffb,#fab64f,#00fffb);
                  background-size: 2500% 100%;
                  animation: change 30s ease infinite alternate;   
            }

            ul.softskilllist{
                  display: flex;
                  transition: .25s linear;
                  background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
                  #ff0000,#00fffb,#fab64f,#00fffb);
                  background-size: 2000% 100%;
                  animation: change 50s ease infinite alternate;   

                  flex-direction: column;
                  padding: 10px;
                  height: 700px;
                  align-items: center;

                  li{
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 500px;
                        background: rgb(167, 189, 235);
                  }
            }
      }
}

@media (max-width: 1100px){
      .pagethree{
            flex-direction: column;
      }
}