.topbg{
    background-color:  rgb(19, 24, 48);
    height: 70px;
    width: 100%;
    position: absolute;
    z-index: 4;
    overflow: hidden;
}

ul.topbarlist{
    height: 70px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;
}

ul.topbarlist a.srssicon{
    position: absolute;
    top: 0;
    left: 10px;
    display: flex;
    padding: 0px;
    border: solid 2px rgb(0, 0, 0);

    img{
        height: 65px;
        width: 100%;
    }
}

ul.topbarlist a.srssicon:hover{
    background-color:black;
    border: solid 2px rgb(255, 255, 255);
}

ul.middlelist{
    display: flex;
    width: 500px;
    justify-content: space-evenly;
}

ul.topbarlist li{
    margin: 4px;
    list-style-type: none;
}

ul.topbarlist a{
    display: flex;
    overflow-y: hidden;
    color: white;
    text-decoration: none;
    font-size: 17px;
    font-weight: 700;
    font-family: Arial, Helvetica, sans-serif;
    padding: 7px 10px;
    border-radius: 30px;
    background-color:black;
}

ul.topbarlist a:hover{
    color: rgb(0, 0, 0);
    transition: .25s linear;
    background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
    #ff0000,#00fffb,#fab64f,#00fffb);
    background-size: 2500% 100%;
    animation: change 25s ease infinite alternate;   
    text-decoration: underline;
}

ul.iconlist{
    display: flex;
}

ul.topbarlist ul.iconlist a{
    height: 60px;
    align-items: center;
    padding: 1px 10px;
    background-color:black;
}

ul.topbarlist a.github:hover{
    transition: .25s linear;
    background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
    #ff0000,#00fffb,#fab64f,#00fffb);
    background-size: 2500% 100%;
    animation: change 25s ease infinite alternate;   
    text-decoration: underline;
}

ul.topbarlist a.linkedin:hover{
    transition: .25s linear;
    background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
    #ff0000,#00fffb,#fab64f,#00fffb);
    background-size: 2500% 100%;
    animation: change 25s ease infinite alternate;   
    text-decoration: underline;
}

.hamburger{
    margin-right: 25px;
    display: block;
    position: fixed;
    right: 0;
    user-select: none;

    appearance: none;
    border: none;
    outline: none;
    background: none;
    cursor: pointer;

    .burgerbox{
        display: flex;
        height: 50px;
        width: 50px;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        border-radius: 12px;
        
        span{
            display: block;
            width: 33px;
            height: 4px;
            margin-bottom: 5px;
            background-color: white;
            border-radius:6px;
        }

        &:hover{
            transition: .25s linear;
            background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
            #ff0000,#00fffb,#fab64f,#00fffb);
            background-size: 2500% 100%;
            animation: change 25s ease infinite alternate;   
            
            span{
                transition: .75s ease;
                background-color: black;
            }
        }
    }
}

@keyframes change{
    0%{
        background-position: 0%;
    }
    50%{
        background-position: 100%;
    }
    100%{
        background-position: 0%;
    }
}

@media (min-width: 768px) {
    .hamburger{
        display: none;
    }
}

@media (max-width: 768px) {
    ul.middlelist{
        display: none;
    }
}