.pageoneblack{
      background-color:  rgb(0, 0, 0);
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;

      .pageonespace{
            display: flex;
            overflow: hidden;
            background-color: rgb(0, 0, 0);
            align-items: center;
            justify-content: center;
            height: 750px;
            width: 1468px;
            
            ul.pageonewrapper{
                  z-index: 1;
                  overflow: hidden;
                  height: 750px;
                  width: 1200px;
                  display: flex;
                  flex-direction: column;
                  align-items: center;
                  justify-content: space-around;
                  color: rgb(255, 255, 255);
                  font-weight: 500;
                  border-radius: 75px;
                  font-family: Arial, Helvetica, sans-serif;
                  font-weight: 700;
                  
                  ul.firstsentence{
                        height: 250px;
                        width: 1000px;
                        display: flex;
                        flex-direction: column;

                        ul.hiseo{
                              display: flex;
                              align-items: center;
                              justify-content: center;
                              overflow: hidden;
                        }
                  }
            }
      }
}

ul.hiseo li.pageoneseo{
      font-size: 65px;
      display: flex;
      align-items: center;
      height: 100px;
      width: 450px;
      justify-content: center;
      background: rgb(167, 189, 235);
      border-radius: 40px;
      color: black;
}

.videogalaxy{
      display: flex;
      position: absolute;
      z-index: 0;
}

ul.pageonewrapper li.simon{
      overflow: hidden;
      color: black;
      display: flex;
      justify-content: center;
      align-content: center;
      width: 350px;
      height: 150px;
      border-radius: 60px;
      font-size: 100px;
      font-family: 'EB Garamond', serif;

      transition: .25s linear;
      background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
      #ff0000,#00fffb,#fab64f,#00fffb);
      background-size: 2500% 100%;
      animation: change 40s ease infinite alternate;   

      @keyframes change{
            0%{
                  background-position: 0%;
            }
            50%{
                  background-position: 100%;
            }
            100%{
                  background-position: 0%;
            }
      }
}

ul.firstsentence li.pageonehi{
      font-size: 60px;
      display: flex;
      justify-content: center;
      align-items: center;
      -webkit-text-stroke-width: 1px;
      -webkit-text-stroke-color:rgb(128, 136, 184);
}

ul.firstsentence li.pageonebut{
      font-size: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      -webkit-text-stroke-width: 1px;
      -webkit-text-stroke-color:rgb(128, 136, 184);
}

ul.pageonewrapper li.developer{
      align-items: center;
      display: flex;
      height: 200px;
      font-size: 35px;
      -webkit-text-stroke-width: 1px;
      -webkit-text-stroke-color:rgb(128, 136, 184);
}

.galaxyvid{
      width: 1475px;
      object-fit: contain;
}

@media (max-width: 850px) {
      ul.firstsentence ul.hiseo li.pageoneseo{
            font-size: 30px;
            height: 50px;
            width: 200px;
      }

      ul.firstsentence li.pageonehi{
            font-size: 30px;
      }

      ul.firstsentence li.pageonebut{
            font-size: 25px;
      }

      ul.pageonewrapper li.developer{
            font-size: 25px;
            height: 50px;
      }

      .pageoneblack{
            .pageonespace{

                  ul.pageonewrapper{
                        height: 500px;
                  }
            }
      }

}