.app{
      height: 100vh;

      .sections{
            width: 100%;
            height: calc(100vh - 70px);
            position: relative;
            top: 70px;
            scroll-snap-type: y mandatory;
            overflow-y: scroll;
            scroll-behavior: smooth;
            scrollbar-width: none; //for firefox
            &::-webkit-scrollbar{
                  display: none;
            }
            
            *{
                  height: calc(100vh - 70px);
                  scroll-snap-align: start;
            }
      }
}