.experiencebgbox{
      display: flex;
      font-family: 'EB Garamond', serif;
      font-size: 25px;
      font-weight: 600;
      background-color: black;
}

.experiencebgbox ul.experiencebg{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-evenly;
      ul{
            z-index: 4;
            display: flex;
      }
}

ul.experiencebg ul.experiencetitlebox{
      display: flex;
      align-items: center;
      padding: 10px 25px;
      height: 100px;
      border-radius: 40px;
      background-color:  rgb(19, 24, 48);
}

ul.experiencetitlebox li.experiencetitle{
      display: flex;
      justify-content: center;
      align-items: center;
      height: 75px;
      font-size: 30px;
      font-weight: 700;
      width: 325px;
      border-radius: 40px;

      transition: .25s linear;
      border: 3px solid black;
      background-image: linear-gradient(45deg,#00fffb,#fab64f,#00fffb,
      #ff0000,#00fffb,#fab64f,#00fffb);
      background-size: 2500% 100%;
      animation: change 30s ease infinite alternate;   
}

